<template>
<div>
  <v-card>
    <v-card-title>
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
        :loading = "isLoadingSearch"
      ></v-text-field>
    </v-card-title>
    <v-data-table
      :headers="headers"
      :items="users"
      :loading = "isLoadingSearch"
      loading-text="Loading... Please wait"
      :page.sync="pagination.current"
      :items-per-page="itemsPerPage"
      hide-default-footer
      class="elevation-1"
    >
        <template v-slot:item.roles="{ item }">
            <v-btn
                small
                icon
                color="green"
                @click="userRoles(item.name,item.key)"
            >
            <v-icon small>mdi-folder-open-outline</v-icon>
            </v-btn>
        </template>
        <template v-slot:item.status="{ item }">
                <v-btn
                small
                outlined
                color="red"
                v-if="item.email_verified_at === null"
                >
                <v-icon small>mdi-check</v-icon>
                Not verified
                </v-btn>
                <v-btn
                small
                outlined
                color="green"
                v-else-if="item.status === '1'"
                >
                <v-icon small>mdi-check-all</v-icon>
                Active
                </v-btn>
                <v-btn
                small
                outlined
                color="pink"
                v-else-if="item.status === '0' "
                >
                <v-icon small>mdi-fire</v-icon>
                Suspended
                </v-btn>
        </template>
        <template v-slot:item.avatar="{ item }">

            <v-list-item-avatar
            :rounded="true"
            @click.stop="open('profile/'+item.key)"
            size="50"
            >
            <v-img :src="$baseUrl+'storage/'+item.avatar"
            ></v-img>
            </v-list-item-avatar>
        </template>
        <template v-slot:item.email_verified_at="{ item }">
             {{formatDate(item.email_verified_at)}}
        </template>
        <template v-slot:item.created_at="{ item }">
            {{formatDate(item.created_at)}}
        </template>
        <template v-slot:item.updated_at="{ item }">
            {{formatDate(item.updated_at)}}
        </template>
        <template v-slot:item.dataBrith="{ item }">
            {{formatBrithDate(item.avatar.dataBrith)}}
        </template>

        <template v-slot:item.type="{ item }">
            <v-btn
            small
            outlined
            color="red"
            v-if="item.type === '1' "
            @click="openUserType(item.key)"
            >
            <v-icon small>mdi-shield-check</v-icon>
            {{ item.user_type}}
            </v-btn>
            <v-btn
            small
            outlined
            color="green"
            v-if="item.type === '2' "
            @click="openUserType(item.key)"
            >
            <v-icon small>mdi-shield-check</v-icon>
            {{ item.user_type}}
            </v-btn>
            <v-btn
            small
            outlined
            color="pink"
            v-if="item.type === '3' "
            @click="openUserType(item.key)"
            >
            <v-icon small>mdi-shield-check</v-icon>
            {{ item.user_type}}
            </v-btn>
        </template>
        <template v-slot:item.cv="{ item }">
            <v-icon v-if="item.cv" small color="green">mdi-check</v-icon>
        </template>

    </v-data-table>
  </v-card>
    <div class="text-center pt-2">
        <v-pagination
            class="pt-6 mt-6"
            v-model="pagination.current"
            :length="pagination.total"
            @input="onPageChange"
            v-show="isSearch"
        ></v-pagination>
        <v-row>
            <v-col
            xs="12"
            sm="1"
            md="1"
            lg="1"
            xl="1"
            >
                <v-text-field
                    :value="total"
                    label="Items"
                    type="number"
                    min="-1"
                    max="500"
                ></v-text-field>

            </v-col>
        </v-row>
    </div>

    <v-dialog
        v-model="dialogRoles"
        max-width="500px"
        persistent
    >
        <v-card>
        <v-card-title>
            <span class="headline">{{userName}}'s Roles</span>
        </v-card-title>

        <v-card-text>
            <v-container>
            <v-row>
                <v-col
                    cols="12"
                >
                <v-autocomplete
                    v-model="editedItem.roles"
                    :items="roles"
                    label="Roles"
                    item-text="name"
                    item-value="id"
                    return-object
                    outlined
                    dense
                    chips
                    small-chips
                    multiple
                >
                </v-autocomplete>
                </v-col>
            </v-row>
            </v-container>
        </v-card-text>

        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
            color="blue darken-1"
            text
            @click="close"
            >
            Cancel
            </v-btn>
            <v-btn
            color="blue darken-1"
            text
            @click="save"
            >
            Save
            </v-btn>
        </v-card-actions>
        </v-card>
    </v-dialog>


    <v-dialog
      v-model="dialogType"
      persistent
      max-width="500px"
    >
      <v-card>
        <v-form ref="priceForm" v-model="valid" lazy-validation>
        <v-card-title>
          <span class="headline">Update User</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-row>
              <v-col cols="12">
                <v-text-field
                  v-model="formList.commets"
                  label="Comments*"
                  required
                  :rules="[rules.required]"
                ></v-text-field>
              </v-col>
            <v-col cols="12">
              <v-autocomplete
                v-model="formList.type"
                :items="userType"
                label="Type"
                item-text="type"
                item-value="id"
                return-object
              >
              </v-autocomplete>
            </v-col>
            <v-col cols="12">
              <v-autocomplete
                v-model="formList.userStatus"
                :items="userStatus"
                label="Status"
                item-text="name"
                item-value="id"
                return-object
              >
              </v-autocomplete>
            </v-col>
            <v-col cols="12">
              <v-autocomplete
                v-model="formList.chefType"
                :items="experiences"
                label="Chef Type"
                :item-text="item => '('+item.ref+')' + item.title + ' - '+ item.description"
                item-value="key"
                return-object
              >
              </v-autocomplete>
            </v-col>
            </v-row>
          </v-container>
          <small>*indicates required field</small>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn
            color="blue darken-1"
            text
            @click="dialogType = false"
          >
            Close
          </v-btn>
          <v-btn
            color="blue darken-1"
            text
            @click="validate"
            :disabled="!valid"
          >
            Save
          </v-btn>
        </v-card-actions>
        </v-form>
      </v-card>
    </v-dialog>
  </div>
</template>
<script>
import moment  from "moment";
  export default {
    data () {
      return {
        itemsPerPage: 50,
        search: '',
        pagination: {
            current: 1,
            total: 0
        },
        headers: [
          {
            text: 'ID',
            align: 'start',
            filterable: true,
            value: 'id',
          },
          { text: 'Avatar', value: 'avatar' },
          { text: 'Name', value: 'name' },
          { text: 'Last Name', value: 'lastName' },
          { text: 'Country', value: 'country' },
          { text: 'Verification', value: 'email_verified_at' },
          { text: 'Updated', value: 'updated_at' },
          { text: 'CV', value: 'cv' },
          { text: 'Type', value: 'type' },
          { text: 'Status', value: 'status' },
          { text: 'Title', value: 'title' },
          { text: 'Roles', value: 'roles' },
          { text: 'Created', value: 'created_at' },
          { text: 'User Name', value: 'userName' },
          { text: 'Email', value: 'email' },
          { text: 'Phone', value: 'phone1' },
          { text: 'Data Brith', value: 'dataBrith' },
          { text: 'Address', value: 'fullAddress' },
        ],
        users: [],
        isSearch: false,
        isLoadingSearch: true,
        total:0,
                userName:null,
                userHasRoles:null,
                editedItem:{
                    roles:null
                },
                roles:[],
                userKey:null,
                experiences:[],
                rules: {
                    required: value => !!value || "Required.",
                    password: value => {
                    const pattern = /^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9])(?=.*[!@#])(?=.{8,})/;
                    return (
                        pattern.test(value) ||
                        "Min. 8 characters with at least one capital letter, a number and a special character."
                    );
                    }
                },
                valid: true,
                formList:{
                    commets:null,
                    type:null,
                    chefType:null,
                    userStatus:null,

                },
                userType:[
                    {type:"Admin",id:"1"},
                    {type:"Client",id:"2"},
                    {type:"Chef",id:"3"}
                ],

                userStatus:[
                    {name:"Suspended",id:"0"},
                    {name:"Active",id:"1"},
                ],
                dialogType:false,
                dialogRoles:false,

      }

    },
    methods: {
        open(url){
            let routeData = this.$router.resolve({path: url});
            var route = routeData.href.replace("pub-order/", "");
            window.open(route, '_self');

        },
        openUserType(key){
            this.userKey = key;

            this.dialogType=true;
        },
        applyFilters(){
            this.isLoadingSearch = true;
            var search
            if(this.search == null){
                search = 'null'
            }else{
                search = this.search
            }
            this.axios.post('/usersPageSearch/'+search,)
                .then(response => {
                    this.users = response.data.data;
                    this.isSearch = true;
                    this.isLoadingSearch = false;
                    this.pagination.current = response.data.current_page;
                    this.pagination.total = response.data.last_page;
                    this.total = response.data.total;
                })
                .catch(() => {
                    this.users = null;
                    this.isSearch = false;
                    this.isLoadingSearch = false;
                });

        },

        getMeals() {
                this.axios.get('/usersPage?page=' + this.pagination.current)
                    .then(response => {
                        this.users = response.data.data;
                        this.isSearch = true;
                        this.isLoadingSearch = false;
                        this.pagination.current = response.data.current_page;
                        this.pagination.total = response.data.last_page;
                        this.total = response.data.total;
                    });
        },
        onPageChange() {
            this.getMeals();
        },
        formatDate(date) {
            return moment(date).format('DD-MM-YYYY HH:mm:ss');
        },
        formatBrithDate(date) {
            return moment(date).format('DD-MM-YYYY');
        },
        userRoles(name,key){
        this.axios
            .get('getUserRoles/'+key)
            .then(response => (this.editedItem.roles = response.data, this.userKey = key,this.userName=name,this.dialogRoles = true));

        },
        close () {
            this.editedItem.roles=null
            this.userName=null
            this.dialogRoles = false
        },
        save () {
            this.axios
            .post("roles/users/create/"+this.userKey, { data: this.editedItem })
            .then(() => (this.close()))
                this.initialize ()
        },
        initialize () {
            this.axios
            .get('getRoles')
            .then(response => (this.roles = response.data));

        },
        onUpdate() {
            this.$refs.table.refresh();
        },
        validate() {
        if (this.$refs.priceForm.validate()) {
            // submit form to server/API here...
            //console.log(this.formReg);
            this.sendData(this.formList);
            this.dialogType = false;
        }
        },
        reset() {
        this.$refs.form.reset();
        },
        resetValidation() {
        this.$refs.form.resetValidation();
        },
        sendData(data) {
        this.axios
        .post("tools/users/update/"+this.userKey, { data: { userData: data} })
        .then(() => {
            this.allerros = [];
            this.sucess = true;
            this.initialize ();
            this.getMeals();
        })
        .catch(() => {
            this.success = false;
        });
    },
    },
    mounted() {
        this.initialize (),
        this.getMeals();
        this.axios
            .get('getExperiencesActive')
            .then(response => (this.experiences = response.data));
    },

    watch: {
        search (val){
            //console.log(this.search);
            if (val.length < 1) return this.getMeals();

            this.applyFilters();
        },
    }
  }
</script>
